import React, {Component} from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin'

import {Row, Col, Breadcrumb, Button} from 'antd';
import {createHashHistory} from "history"


import './style.scss';


class Content extends Component {
    constructor(props) {
        super(props);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this)
        this.state = {
            showAll: "block",
            headerNav: [{
                name: ""
            }, {
                name: ""
            }, {
                name: ""
            }],

        }
    }

    toggleLogin = () => {
        this.props.logFn();
    }

    getChainsList = () => {
        const data = this.props.chainsList;

        if (data && data.length) {
            return data
        }

    }

    tableView(data, id) {
        console.log(id)
        this.props.showChainsTable(data, id);
    }

    goBack() {
        createHashHistory().replace("/platform/contract");
    }

    hideShowButton = (index, types) => {
        this.props.showAndHide(index, types);
    }

    getContractShopList = () => {
        let data = this.props.contractShopList;

        return data;
    }

    render() {
        return (
            <Row type="flex" justify="center" className="verify-content">
                <Col className="top" span={16}>
                    <Breadcrumb separator=">">
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/platform"></Breadcrumb.Item>
                        <Breadcrumb.Item className="BreadcrumbItem" href="/#/platform/contract"></Breadcrumb.Item>
                        <Breadcrumb.Item className="BreadcrumbItem"
                                         href="/#/platform/contract/buysuccess"></Breadcrumb.Item>
                    </Breadcrumb>

                    <h2 className="title">.</h2>

                    <Row type="flex" justify="center" className="chains-intent">
                        <Col span={18} className="content">
                            <div className="section">
                                <div>
                                    <span className="label">ID:</span>
                                    <span>{this.props.orderInfo.id}</span>
                                </div>
                                <div className="pulisher">
                                    <span className="label">:</span>
                                    <span>{this.props.orderInfo.publisher}</span>
                                </div>
                            </div>

                        </Col>
                    </Row>
                    <div className="button-content">
                        <Button className="Back" onClick={this.goBack}></Button>
                    </div>


                </Col>
            </Row>


        )
    }
}

export default Content;


